@import url(https://fonts.googleapis.com/css?family=Lato:400,700,700i,900,900i);
.ali-logo {
  width: 38px;
  height: 32px;
}
.social-media-icons .social-media-icon {
  fill: #babcc5;
}
.social-media-icons a {
  margin: 0 10px;
}
.header {
  background-color: #1d1e2b;
  color: #babcc5;
}
.header a {
  color: #babcc5;
}

@media (max-width: 600px) {
  .header {
    display: flex;
    flex-direction: column;
    height: 50px;
  }
  .header .header-logo {
    margin: 6px 0 11px 40px;
  }
  .header .header-items-hide {
    display: none;
  }
  .header .header-items-show {
    display: flex;
    flex-direction: column;
    background-color: #1d1e2b;
    opacity: 0.9;
  }
  .header .header-items-show ul {
    background-color: #1d1e2b;
    display: flex;
    flex-direction: column;
    list-style-type: none;
    font-size: 21px;
    font-weight: bold;
    padding: 0;
    margin: 0;
  }
  .header .header-items-show ul li {
    display: flex;
    justify-content: center;
    height: 120px;
    border: 1px solid rgba(186, 188, 197, 0.3);
    align-items: center;
  }
  .header .header-items-show .social-media-icons {
    background-color: #1d1e2b;
    display: flex;
    justify-content: center;
    padding: 60px 0;
    border: 1px solid rgba(186, 188, 197, 0.3);
    align-items: center;
  }
  .header .icon-toggle {
    align-self: flex-end;
    display: inline;
    display: initial;
    position: absolute;
    cursor: pointer;
    top: 6px;
    left: 330px;
  }
}
@media (min-width: 600px) {
  .header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 72px;
  }
  .header .header-item-status {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .header ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    margin-left: 80px;
    font-size: 21px;
    font-weight: bold;
  }
  .header ul li:not(:first-of-type) {
    border-left: 1px solid #babcc5;
  }
  .header ul li {
    padding: 0 30px;
    display: flex;
    align-items: center;
  }
  .header .social-media-icons {
    margin-left: 150px;
  }
  .header .icon-toggle {
    display: none;
  }
}
@media (min-width: 1730px) {
  .header {
