button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 45px;
  background-color: #ff7250;
  color: #ffffff;
  font-size: 22px;
  font-style: bold;
  border: 1px solid;
  border-radius: 10px;
  margin-top: 40px;
}
button:hover {
  background-color: #f15a35;
  cursor: pointer;
}
