html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Lato, sans-serif;
  font-size: 16px;
  color: #1d1e2b;
  max-width: 100%;
  min-width: 100%;
}

button {
  outline: none;
}

#root {
  width: 100%;
}

a {
  text-decoration: none;
}

.logo-path {
  fill: #babcc5;
}

@media (min-width: 600px) {
